<template lang="html">
  <only-container>
    <only-loading
    :loading="loading">
      <only-article
        v-if="!notExisted && post"
        :article="post"
        :config="config"
      >
      </only-article>
      <div v-if="notExisted">
        <h2>  </h2>
        <p><router-link
            :to="{name: 'Posts'}"
          ></router-link></p>
        <img src="https://as.bitinn.net/upload/ciodybere00a528s5yot7bffd.1200.jpg" alt="">
      </div>
    </only-loading>
  </only-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import ArticleItem from '@/components/ArticleItem'
import Container from '@/components/Container'
import Loading from '@/components/Loading'

export default {
  props: [ 'slug' ],
  created () {
    this.fetchPost({ slug: this.slug })
      .then(() => {
        document.title = `${this.post.title} | ${this.siteCfg.title}`
        this.loading = false
      })
      .catch((err) => {
        if (err.response && err.response.status === 404) {
          this.notExisted = true
        }
        this.loading = false
      })
  },
  components: {
    'only-article': ArticleItem,
    'only-container': Container,
    'only-loading': Loading
  },
  data () {
    return {
      loading: true,
      notExisted: false
    }
  },
  computed: {
    ...mapGetters([ 'post', 'themeCfg', 'siteCfg' ]),
    config () {
      return {
        shortname: this.themeCfg.disqus_shortname,
        url: window.location.href,
        title: document.title,
        identifier: window.location.href
      }
    }
  },
  methods: {
    ...mapActions([ 'fetchPost' ])
  }
}
</script>

<style lang="css">
</style>



